<template>
  <div class="Authentication">
    <!--      <HomePageTop :TopTitle="TopTotle"></HomePageTop>-->
    <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
    <div class="bg_f4f6f9 pt24 pb85">
      <van-uploader style="width :100%" :after-read="HeadTopClick">
        <div class="uploadingHead">
          <p>上传头像</p>
          <div class="dib">
            <img :src="HeadTopImg" alt="" class="dib vm" style="height: 1.28rem;" />
            <i class="iconfont"></i>
          </div>
        </div>

      </van-uploader>

      <p style="line-height: 0.86rem;height: 0.86rem;" class="cl_999 pl30 f28"><i class="iconfont">&#xe678;</i>
        您的资料已进行安全保护</p>

      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>昵称</p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" placeholder="请输入账号" v-model="nickname">
            <i class="iconfont op0"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8" @click="SelectShow = true">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>性别</p>
          <div class="cl_333">
            {{SelectText}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8" @click="Timeshow = true">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>生日</p>
          <div>
            {{TimeText}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>QQ</p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
            <input type="text" style="border: none;text-align: right" v-model="QQTime">
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8" @click="AccountShow = true">
          <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>账号类型</p>
          <div>
            {{AccountText}}
            <i class="iconfont"></i>
          </div>

        </div>
      </div>



      <div class="mt24" v-if="AccountText=='个人'">
        <!--姓名-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>姓名</p>
            <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
              <input type="text" style="border: none;text-align: right" placeholder="请输入账号" v-model="MeName">
              <i class="iconfont op0"></i>
            </div>
          </div>
        </div>
        <!--身份证号-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>身份证号</p>
            <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
              <input type="text" style="border: none;text-align: right" placeholder="请输入" v-model="identityCard">
              <i class="iconfont op0"></i>
            </div>
          </div>
        </div>
        <!--工作性质-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8" @click="personage(0)">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>工作性质</p>
            <div>
              <span class="dib" :class="NatureText=='请选择'?'cl_ddd':'cl_333'">{{NatureText}}</span>
              <i class="iconfont"></i>
            </div>
          </div>
        </div>
        <!--应用领域-->
        <div class="fiexoverTitle">
          <div class="fiexover" @click="personage(1)">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>应用领域</p>
            <div>
              <span class="dib" :class="domain=='请选择'?'cl_ddd':'cl_333'">{{domain}}</span>
              <i class="iconfont"></i>
            </div>
          </div>
        </div>
      </div>
      <div class="mt24" v-else>
        <!--公司名称-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>公司名称</p>
            <div style="height: 100%;line-height: 1.02rem;    width: 70%;" class="f34 cl_333">
              <input type="text" style="border: none;text-align: right;    width: 100%;" v-model="companyName" placeholder="请填写营业执照上的公司名称">
              <i class="iconfont op0"></i>
            </div>
          </div>
        </div>
        <!--统一社会信用代码-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8">
            <p style="width: 50%"><i style="color: red;vertical-align: middle;display: inline-block;">*</i>统一社会信用代码</p>
            <div style="height: 100%;line-height: 1.02rem;width: 50%;text-align: right" class="f34 cl_333">
              <input type="text" style="border: none;text-align: right;width: 60%" v-model="CreditCode" placeholder="请输入">
              <i class="iconfont op0"></i>
            </div>
          </div>
        </div>
        <!--企业类型-->
        <div class="fiexoverTitle">
          <div class="fiexover border_E3E4E8" @click="personage(0)">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>企业类型</p>
            <div>
              <span class="dib" :class="NatureText=='请选择'?'cl_ddd':'cl_333'">{{NatureText}}</span>
              <i class="iconfont"></i>
            </div>
          </div>
        </div>
        <!--所属行业-->
        <div class="fiexoverTitle">
          <div class="fiexover" @click="personage(1)">
            <p><i style="color: red;vertical-align: middle;display: inline-block;">*</i>所属行业</p>
            <div>
              <span class="dib" :class="domain=='请选择'?'cl_ddd':'cl_333'">{{domain}}</span>
              <i class="iconfont"></i>
            </div>
          </div>
        </div>
        <!-- 认证图片-->
        <div class="bg_fff pt30 pl30 pr30  pb30 ">
          <p class="f34 cl_666 mb35">
            认证图片<span class="vm f24 cl_999">注:照片支持jpg/png/gif，文件大小不超过5M</span>
          </p>
          <van-uploader :after-read="afterRead"  multiple :max-count="2"  :max-size="500 * 1024"  @oversize="onOversize" class="bg_fff vm" preview-full-image  accept="image/*">
            <img src="../../assets/img/PersonalCenter/uploadingimg.png" style="width: 1.44rem;height: 1.44rem"/>
          </van-uploader>
          <div v-for="(data,index) in fileList" class="dib vm">
            <img :src=data.content alt="" style="width: 1.5rem;height: 1.5rem">
          </div>
          <p>营业执照复印件加盖公司公章（如图所示）</p>
        </div>
        <div class="bg_fff pt30 pl30 pr30 mt10 pb30">
          <p class="f34 cl_666 mb35">
            公司证明文件或对公打款流水单截图
            <br>
            <span class="vm f24 cl_999">注:照片支持jpg/png/gif，文件大小不超过5M</span>
          </p>
          <div class="mb22">

            <van-uploader :after-read="afterApproveRead"  multiple :max-count="2"  :max-size="500 * 1024"  @oversize="onApproveOversize" class="bg_fff vm" preview-full-image  accept="image/*">
              <img src="../../assets/img/PersonalCenter/uploadingimg.png" style="width: 1.44rem;height: 1.44rem"/>
            </van-uploader>
            <div v-for="(data,index) in fileApproveList" class="dib vm">
              <img :src=data.content alt="" style="width: 1.44rem;height: 1.44rem">
            </div>
          </div>

          <p class="cl_f90 f24">下载企业认证申请函模板</p>
        </div>

<!--        <van-uploader />-->
      </div>
      <div class="mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick">提交</div>
    </div>
    <van-popup v-model="Timeshow" position="bottom">
      <van-datetime-picker v-model="currentDate" type="date" title="" :min-date="minDate" :max-date="maxDate"
                           confirm-button-text="确定" @confirm="TimeYes" @cancel="Timeshow=false" />
    </van-popup>
    <!--姓名弹出层-->
    <van-action-sheet v-model="SelectShow" :actions="actions" @select="onSelect" :round=false />
    <!--账号类型弹出层-->
    <van-action-sheet v-model="AccountShow" :actions="AccountActions" @select="onAccountSelect" :round=false />
    <!--工作性质弹出层-->
    <van-popup v-model="Natureshow" round position="bottom">
      <van-picker show-toolbar :columns="columns" @cancel="Natureshow = false" @confirm="NatureConfirm" v-if="oneself==true"></van-picker>
      <van-picker show-toolbar :columns="columns_box" @cancel="Natureshow = false" @confirm="NatureConfirm" v-else></van-picker>
    </van-popup>
    <!--    个人中心的企业类型和所属行业-->
    <van-popup v-model="firmshow" round position="bottom">
      <van-picker show-toolbar :columns="firmshowcolumns" @cancel="firmshow = false" @confirm="NatureConfirm" v-if="firmeself==true"></van-picker>
      <van-picker show-toolbar :columns="firmshowcolumns_box" @cancel="firmshow = false" @confirm="NatureConfirm" v-else></van-picker>
    </van-popup>

  </div>


</template>

<script>
  import Vue from "vue"
  import HomePageTop from "../common/HomePageTop";
  import $ from "jquery"

  export default {
    name: "Authentication",
    components: {
      HomePageTop
    },
    data() {
      return {
        //像子组件传值
        TopTotle: { name: "认证中心",left: true},
        //姓名
        nickname:"",
        //性别筛选
        SelectShow: false,
        actions: [{name: '男'}, { name: '女'}, ],
        SelectText: "",
        //时间
        Timeshow: false,
        minDate: new Date(1949, 0, 1),
        maxDate: new Date(),
        currentDate: new Date(),
        //生日具体数值
        TimeText: "",
        //QQ
        QQTime:"",
        // 账号类型
        AccountShow: false,
        AccountActions: [{name: '企业'}, {name: '个人'}, ],
        AccountText: "个人",
        //  个人
        //我的名字
        MeName:"",
        //身份证号
        identityCard:"",
        //  工作性质

        NatureText: '请选择',
        //工作领域
        domain: "请选择",
        Natureshow: false,
        columns: ['无业游民', '划水', '摸鱼', '咸鱼', '摸螃蟹', '打鱼', '养鱼', '池塘'],
        columns_box: ['科学计算', '数据处理', '人工智能', '生产自动化', '摸螃蟹', '打鱼', '养鱼', '池塘'],
        oneself: true,
        //企业
        //企业名称
        companyName:"",
        //企业信用代码
        CreditCode:"",
        //企业弹出层
        firmshow:false,
        firmshowcolumns:['1111', '2222', '3333', '4444', '5555', '6666', '777', '8888'],
        firmshowcolumns_box:['123', '234', '345', '456', '567', '678', '789'],
        firmeself:true,
        NatureText: '请选择',
        //工作领域
        domain: "请选择",
        //  文件上传
        fileList: [],
        //公司证明文件或者对公打款流水单截图
        fileApproveList:[],
        //上传头像
        HeadTopImg:"",
      //  提交的颜色以及提交
        SubmitClassbox:false,
      }
    },
    watch: {
      AccountText(val) {this.AUnifiedCall();},
      nickname(val) {this.AUnifiedCall();},
      SelectText(val) {this.AUnifiedCall();},
      TimeText(val) {this.AUnifiedCall();},
      QQTime(val) {this.AUnifiedCall();},
      MeName(val) {this.AUnifiedCall();},
      identityCard(val) {this.AUnifiedCall();},
      NatureText(val) {this.AUnifiedCall();},
      //企业名称
      companyName(val) {this.AUnifiedCall();},
      //企业信用代码
      CreditCode(val) {this.AUnifiedCall();},
      //企业类型
      NatureText(val) {this.AUnifiedCall();},
      HeadTopImg(val){this.AUnifiedCall();}
    },
    methods: {
      //提交存入vuex中
      SubmitClassClick(){
        let HomeData=[];


        if(this.AccountText=="个人"){

          HomeData=[
            {
              "passGo":true,
            "HeadTopImg":this.HeadTopImg,
            "nickname": this.nickname,
            "SelectText":this.SelectText,
            "TimeText": this.TimeText,
            "QQTime": this.QQTime,
            "MeName" :this.MeName,
            "identityCard" :this.identityCard,
            "NatureText" :this.NatureText,
            "domain":this.domain},],
          console.log(HomeData);

          this.$store.commit("HomeDatas",HomeData);
        }else{
          HomeData=[{
            "passGo":true,
            "HeadTopImg":this.HeadTopImg,
             "nickname":this.nickname,
            "SelectText":this.SelectText,
            "TimeText":this.TimeText,
            "QQTime":this.QQTime,
            "companyName":this.companyName,
            "CreditCode":this.CreditCode,
            "NatureText":this.NatureText,
            "domain":this.domain,
            "fileList":this.fileList,
            "fileApproveList":this.fileApproveList
        }];
          this.$store.commit("HomeDatas",HomeData);
        }



      },
      AUnifiedCall(){
        //是个人的时候
        if(this.AccountText=="个人"){
          console.log(this.nickname,this.SelectText,this.TimeText,this.QQTime,this.MeName,this.identityCard,this.NatureText,this.domain);
          if(this.HeadTopImg!=""&&this.nickname!=""&&this.SelectText!=""&&this.TimeText!=""&&this.QQTime!=""&&this.MeName!=""&&this.identityCard!=""&&this.NatureText!="请选择"&&this.domain!="请选择"){
            alert("个人发起提交")
            this.SubmitClassbox=true;
          }else{

          }
        }else{
          // console.log(this.nickname,this.nickname,this.TimeText,this.QQTime,this.companyName,this.CreditCode,this.NatureText,this.domain,this.fileList.length,this.fileList.length,);

          if(this.HeadTopImg!=""&&this.nickname!=""&&this.SelectText!=""&&this.TimeText!=""&&this.QQTime!=""&&this.companyName!=""&&this.CreditCode!=""&&this.NatureText!="请输入"&&this.domain!="请选择"&&this.fileList.length>0&&this.fileApproveList.length>0){
            alert("企业发起提交");
            this.SubmitClassbox=true;

          }else{

          }
        }
      },
      //文件上传
      afterRead(file) {
        this.AUnifiedCall();
        // 此时可以自行将文件上传至服务器
        if(this.fileList.length>1){
          return;
        }
        this.fileList.push(file);
      },
      //上传头像
      HeadTopClick(file){
        console.log(file);
        this.HeadTopImg=file.content;
      },
      afterApproveRead(file){


        if(this.fileApproveList.length>1){
          return;
        }else{
          this.fileApproveList.push(file);
        }
        this.AUnifiedCall();
      },
      onApproveOversize(file){
        console.log(file);
        // console.log('文件大小不能超过 500kb');
        this.AUnifiedCall();
      },
      onOversize(file) {
        console.log(file);
        // console.log('文件大小不能超过 500kb');
        this.AUnifiedCall();
      },
      //触发弹出层
      personage(i) {
        if(this.AccountText=="个人"){
          this.Natureshow = true;
          if (i == 0) {
            this.oneself = true;
          } else {
            this.oneself = false
          }
        }else{
          this.firmshow = true;
          if (i == 0) {
            this.firmeself = true;
          } else {
            this.firmeself = false
          }
        }
        this.AUnifiedCall();

      },
      //工作性质-----个人及企业判断
      NatureConfirm(value) {
        //个人调用
        if(this.AccountText=="个人"){
          this.Natureshow = false;
          if (this.oneself == true) {
            this.NatureText = value;
          } else {
            this.domain = value;
          }
          //  企业的调用
        }else{
          this.firmshow = false;
          if (this.firmeself == true) {
            this.NatureText = value;
          } else {
            this.domain = value;
          }
        }
        this.AUnifiedCall();
      },
      //点击生日确定按钮
      TimeYes(value) {
        let d = value;
        let objx = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
        console.log(objx);
        this.TimeText = objx;
        this.Timeshow = false;
        this.AUnifiedCall();
      },
      //性别弹出层
      onSelect(item) {
        // 默认情况下点击选项时不会自动收起
        // 可以通过 close-on-click-action 属性开启自动收起
        this.SelectShow = false;
        this.SelectText = item.name;
        this.AUnifiedCall();
      },
      //账号类型弹出层
      onAccountSelect(item) {
        this.AccountShow = false;
        this.AccountText = item.name;
        //  工作性质
        this.NatureText="请选择";
        this.domain="请选择";
        this.AUnifiedCall();
      },
    },
    mounted() {


      // window.onload = function(){
      //   $("#a").click(function(){
      //     //adding your code here
      //   });
      //   $("body").onload(function(){
      //
      // })

    },
    created() {

    }
  }
</script>

<style>
  /*上传头像*/

  .uploadingHead {
    display: flex;
    justify-content: space-between;
    height: 1.76rem;
    line-height: 1.76rem;
    color: #333;
    width: 100%;
    padding: 0 0.3rem;
    background: #fff;
    font-size: 0.34rem
  }

  input {
    border: none;
    color: #333333;
    font-size: 0.32rem;
    outline: none;
    vertical-align: text-bottom;
    padding-left: 0.3rem;
    line-height: initial;
  }
  .HomePageTop .Head{
    background: #fff ;
  }
  .van-uploader__input-wrapper{
    width: 100%;
  }
</style>



